<template lang="html">
  <div class="">
    <articles :articles="articlesData"></articles>
  </div>

</template>

<script>
import Comment from './components/Comment'
import Articles from './components/Articles'
import faker from '../static/faker'

export default {
  components:{
    Articles,Comment
  },
  data () {
    return {
      //articlesData:{},
      articlesData:String,
      commentList:[]
    }
  },
  created () {
    this.axios.get('https://www.easy-mock.com/mock/5ae9e34bbd50881f100270e3/example/restful/:1234/list').then(res=>{
    //this.articlesData=res.data.match(/<section(.*)?<\/section>/)[0];
    //console.log(res.data.match(/<section(.*)?<\/section>/));
      console.log(res);
    })
    this.articlesData = faker.articlesData();
  }
}
</script>

