<template lang="html">
  <div class="comments_container">
    <div class="lists clearFix" v-for="list in lists">
      <div class="author">
        <img :src="list.faceImg" alt="">
      </div>
      <div class="info">
        <a>{{ list.userName }}</a>
        <div class="text">{{ list.content }}</div>
      </div>

    </div>
    <div class="page">
      <a v-if="previous">上一页</a>
      <a v-for="(index) in pages">{{index}}</a>
      <span v-if="more">...</span>
      <a>{{ totalPages }}</a>

      <a v-if="next">下一页</a>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    lists: Array
  },
  data () {
    return{
      previous:false,
      next:true,
      more:true,
      totalPages:25,
      pages:[1,2,3,4,5]
    }

  }
}
</script>

<style lang="scss" scoped>
  .comments_container{
    margin-top: 20px;
    .lists{
      margin-bottom: 26px;
      color:#fff;
      .author{
        float: left;
        margin-top: 16px;
      }
      .info{
        margin-left: 80px;
        background: #1fb5ad;
        padding:16px;
        min-height: 60px;
        .text{
          margin-top: 7px;
        }
      }
  }
}
.page{
  float: right;
  color:#fff;
  a,span{
    margin-left: 5px;
  }
}
</style>



