<template>
  <div>
    <!-- 分区 -->
    <patition :menuList="patitionImgData">
      <h2>分区</h2>
    </patition>
    <div class="clearFix">
      <!-- 专栏 -->
      <sp-colum class="col-5 spcol" :lists="spColData">
        <h2>专栏</h2>
      </sp-colum>
      <!-- 直播 -->
      <live class="col-5 live" :lists="liveData">
        <h2>直播</h2>

      </live>
    </div>

  </div>

</template>
<script>
  import Patition from './components/Patition.vue'
  import SpColum from './components/SpColum.vue'
  import Live from './components/Live.vue'
  import faker from "../static/faker"

  export default {
    components:{
      Patition,
      SpColum,
      Live
    },
    data () {
      return {
        patitionImgData: [],
        liveData: [],
        spColData: []
      }
    },
    created () {
        this.patitionImgData = faker.patitionImgData();
        this.liveData = faker.liveData();
        this.spColData = faker.spColData();
        this.axios.get('/news/latest').then(function(res){
          console.log(res);
        })
        this.axios.get('/query/rank').then(function(res){
          console.log(res);
        })
        fetch('http://webapi.acfun.cn/query/rank',{mode:'no-cors'}).then(res=>{console.log(res)})
    }
}
</script>
<style scoped lang="scss">
.col-5{
    float: left;
    width: 50%;
  }
.spcol{
  padding-right: 43px;
}
.live{
  padding-left: 43px;
}
h2{
    text-align: center;
}
</style>



